.header {
	height: 54px;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-content: center;
	align-items: center;

	& .search {
		//font-size: 11pt;
		width: 152px;
		background-color: #F1F1F1;
		height: 32px;
		border-radius: 4px;
		display: flex;
		color: #999999;
		align-content: center;
		align-items: center;
		cursor: text;
		& span {
			font-size: 11pt;
			margin-left: 14px;
		}
		// TODO flex-grow: 1;
	}

	& .burger {
		width: 18px;
		height: 18px;
		padding-top: 3px;
		& div {
			width: 18px;
			height: 2px;
			background-color: #A8A8A8;
			margin-bottom: 3px;
		}
		cursor: pointer;
	}
}

