.chatList {
	width: 260px;
	height: 100vh;
	background-color: #FFFFFF;
	display: flex;
	flex-direction: column;
	flex-shrink: 0;
	//box-shadow: 8px 8px 2px 20px red;
	//z-index: 10;
	//border-right: 1px;
	//border-color: rgb(235, 235, 235);
	//border-style: solid;
	//box-sizing: border-box;
	//background-clip: padding-box;
	position: relative;
}

.chats {
	height: calc(100vh - 54px);
	width: 260px;
	position: relative;
}

.chats .chatListScrollBar, .chats .chatListScrollBarSlider {
	opacity: 0;
}

.chats:hover .chatListScrollBar, .chats:hover .chatListScrollBarSlider {
	opacity: 1;
}

.chatListScrollBarSlider {
	content: "";
	display: block;
	width: 4px;
	background-color: rgba(0, 0, 0, 0.1);
	position: absolute;
	right: 3px;
	top: 3px;
	height: 100px;
	border-radius: 4px;
	transition: background-color 0.5s ease, opacity 0.5s ease;
}

.chatListScrollBarSlider:hover {
	background-color: rgba(0, 0, 0, 0.4);
}

.chatListScrollBar {
	display: block;
	width: 4px;
	background-color: rgba(0, 0, 0, 0.1);
	height: calc(100% - 6px);
	position: absolute;
	right: 3px;
	top: 3px;
	border-radius: 4px;
	transition: background-color 0.5s ease, opacity 0.5s ease;
}

.chatListScrollBar:hover {
	background-color: rgba(0, 0, 0, 0.25);
}

.chatListScrollPane {
	position: relative;
	top: 0px;
	transition: top 0.4s ease;
	height: auto;
}

.chatListElement img.avatar {
	width: 46px;
	height: 46px;
	background-color: gray;
	border-radius: 100%;
	flex-shrink: 0;
	margin-right: 12px;
}

.chatListElement div.avatarEmpty {
	width: 46px;
	height: 46px;
	background-color: #7BC862;
	border-radius: 100%;
	flex-shrink: 0;
	margin-right: 12px;
	font-size: 18px;
	color: white;
	display: flex;
	justify-content: center;
	flex-direction: column;
}

.chatListElement span {
	font-size: 11pt;
	white-space: nowrap;
	display: inline-flex;
}

.chatListElement span.bold {
	color: black;
	font-weight: 500;

	text-overflow: ellipsis;
}

.chatListElement span.light {
	color: #919191;
}

.chatListElement span.date {
	align-self: flex-end;
	flex-shrink: 0;
}

.chatListElement span.name {
	flex-shrink: 1;
	position: relative;
}

.chatListElement .textcounter {
	display: flex;
	//width: calc(100% - 40px);
	justify-content: space-between;
	//background-color: #01F1F1;
	//flex-shrink: 0;
}

.chatListElement .textcounter .text {
	//width: 80%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	display: inline-flex;
}

.chatListElement .textcounter .text div {
	display: inline-block;
	font-size: 11pt;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.chatListElement .textcounter .text div.who {
	color: #16A0E7;
	margin-right: 4px;
	text-overflow: unset;
	flex-shrink: 0;
}

.chatListElement .textcounter .text div.draft {
	color: #DD4B39;
	margin-right: 4px;
	text-overflow: unset;
	flex-shrink: 0;
}


