.chatListElement {
	width: 260px;
	height: 62px;
	cursor: pointer;
	display: flex;
	//padding-right: 10px;
	//padding-left: 10px;
	justify-content: center;
	align-content: center;
	flex-direction: row;

	background-color: rgba(0,0,0,0);
	transition: background-color 0.15s ease-in-out;
}

.chatListElement__active {
	background-color: #419FD9;
	color: white !important;
}

.chatListElement__active * {
	color: white !important;
}

.chatListElement__active * .unread div {
	color: #419FD9 !important;
}

.chatListElement:hover {
