.history {
	//display: flex;
	//flex-direction: column-reverse;
	width: 100%;
	height: calc(100vh - 54px);
	//height: auto;
	position: relative;
}

.historyView {
	width: 100%;
	//overflow-y: auto;
	//display: flex;
	position: relative;
	height: auto;
	flex-shrink: 0;

	top: 0px;
	transition: top 0.4s ease;

	display: flex;
	flex-direction: column;
}

.history .chatListScrollBar, .history .chatListScrollBarSlider {
	opacity: 0;
}

.history:hover .chatListScrollBar, .history:hover .chatListScrollBarSlider {
	opacity: 1;
}

