.centerBackgroundBefore {
	content: "";
	background-color: #27443A;
	position: absolute;
	left: 0px;
	top: 0px;
	width: 100%;
	height: 100%;
	z-index: -2;
}

.centerBackground {
	//display: none;
	z-index: -1;
	//display: flex;
	//filter: blur(20px);
	transition: filter 1s ease, transform 1s ease;
	//position: relative;
	position: absolute;
	left: 0%;
	top: 0px;
	width: 100%;
	height: 100%;
	//background-color: #2D5044;
	background-image: url(/dark-fog-forest-haze-6992.jpg);
	background-repeat: no-repeat;
	background-size: cover;
	//backdrop-filter: blur(10px);

	transform: scale(1.1);
}

.pleaseSelectChat  {
	width: 100%;
	height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	& div, & a {
		display: flex;
		color: white;
		font-size: 10.5pt;
		background-color: rgba(0, 0, 0, 0.2);
		border-radius: 12px;
		padding: 4px 12px;
		flex-grow: 0;
		text-decoration: none;
	}
}
