//	font-size: 11pt;
	display: flex;
	//align-self: center;
	flex-direction: row;
	//align-content: center;
	justify-content: center;
	//flex-grow: 1;
	//width: 100%;
	//text-align: center;
	margin-top: 8px;
	margin-bottom: 4px;
	& div {
		display: flex;
		color: white;
		font-size: 10.5pt;
