.centerSystemMessageGroupPic {
	display: flex;
	flex-direction: row;
	justify-content: center;
	margin-top: 8px;
	margin-bottom: 4px;
	& div {
		display: flex;
		width: 100px;
		height: 100px;
		border-radius: 100%;
		background-size: contain;
		background-color: transparent;
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
	}
}

.stickerMy {
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	margin-top: 8px;
	margin-bottom: 4px;
	margin-right: 16px;
	position: relative;

	& div.webp {
		display: flex;
		//width: 100px;
		width: 256px;
		//height: 100px;
		height: 256px;
		//max-width: 220px;
		//border-radius: 100%;
		background-color: transparent;
		//background-image: url(/flowers.jpg);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
		cursor: pointer;
	}

	& div.time {
		position: absolute;
		pointer-events: none;
		font-size: 11pt;
		color: white;
		border-radius: 10px;
		height: 22px;
		background-color: rgba(0, 0, 0, 0.2);
		width: auto;
		bottom: 0px;
		right: 0px;
		padding-bottom: 20px;
		padding-top: 4px;
		padding-left: 7px;
		padding-right: 7px;

		display: none;
	}

	&:hover div.time {
		display: block;
	}
}

