.messageAvatar {
	//transition: bottom 0.4s ease-in-out;
	//transition: top 0.4s ease-in-out;
	width: 33px;
	height: 33px;
	border-radius: 100%;
	//float: left;
	position: absolute;
	//bottom: 1px;
	left: 13px;
	top: calc(100% - 34px);
	cursor: pointer;
}

.messageAvatarPlaceholder {
	//transition: bottom 0.4s ease-in-out;
	//transition: top 0.4s ease-in-out;
	width: 33px;
	height: 33px;
	border-radius: 100%;
	//float: left;
	position: absolute;
	//bottom: 1px;
	left: 13px;
	top: calc(100% - 34px);
	display: block;
	font-size: 13px;
	background-color: #7BC862;
	display: flex;
	justify-content: center;
	flex-direction: column;
	color: white;
	flex-shrink: 0;
	cursor: pointer;
}

