.messageTheirsPhoto {
	padding-right: 0px;
	padding-left: 0px;
	padding-top: 0px;
	position: relative;

	& .author {
		padding: 8px;
		padding-left: 14px;
	}

	& .right {
		padding-right: 8px;
		padding-top: 8px;

	}
	& .text {
		padding-left: 8px;
		padding-top: 8px;

	}

}

.messageTheirsPhotoNoCaption {
	padding: 0px;

	& .right {
		position: absolute;
		bottom: 10px;
		right: 10px;
		//display: none !important;
		visibility: hidden;

		& .time {
			color: white !important;
			background-color: rgba(0, 0, 0, 0.2);
			border-radius: 8px;
			padding-bottom: 3px;
			padding-top: 4px;
			padding-left: 7px;
			padding-right: 7px;
		}

	}

	&:hover .right {
		//display: flex;
		visibility: visible;
	}

	& .text {
		display: none !important;
	}
}

